.logo {
  width: min(50vw, 60px);
  cursor: pointer;
  margin-left: auto;
  fill: #f0f0f0;
  stroke-width: 10px;
  stroke: #f0f0f0;
  transition: all 0.3s ease-in-out;
}

.logo .cls3 {
  transition: all 0.3s ease-in-out;
}

.logo:hover .cls3 {
  fill: transparent;
  stroke: url(#cols1);
  stroke-width: 10px;
  transition: all 0.3s ease-in-out;
}

.logo .cls1 {
  transition: all 0.4s ease-in-out;
}

.logo:hover .cls1 {
  fill: transparent;
  stroke: url(#cols2);
  stroke-width: 10px;
  transition: all 0.4s ease-in-out;
}

.logo .cls2 {
  transition: all 0.5s ease-in-out;
}

.logo:hover .cls2 {
  fill: transparent;
  stroke: url(#cols3);
  stroke-width: 10px;
  transition: all 0.5s ease-in-out;
}

@media (max-width: 800px) {
  .logo {
    display: none;
  }
}
